import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Persons from '@/components/Persons'
import PersonAdd from '@/components/PersonAdd'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/persons',
      name: 'Persons',
      component: Persons
    },
    {
      path: '/persons/add',
      name: 'PersonAdd',
      component: PersonAdd
    }
  ]
})




