<template>
	<section>
		<h1>Список персон</h1>
		<div class="posts">
	  		<table class="table">
	  			<thead>
	  				<tr>
	  					<th>#</th>
	  					<th>Имя</th>
	  					<th>Группа крови</th>
	  					<th>FIN</th>
	  				</tr>
	  			</thead>
	  			<tbody>
	  				<tr v-for="(person, index) in persons">
	  					<td scope="row">{{index + 1}}</td>
	  					<td>{{person.firstName + " " + person.lastName}}</td>
	  					<td>{{person.bloodType}}</td>
	  					<td>{{person.fin}}</td>
	  				</tr>
	  			</tbody>
	  		</table>
	  	</div>
	</section>
</template>

<script>
import PersonsService from '@/services/PersonsService'

export default {
  name: 'Persons',
  data () {
    return {
    	persons: []
    }
  },
  mounted () {
    this.getPersons()
  },
  methods: {
    async getPersons() {
      const response = await PersonsService.fetchPersons();
      this.persons = response.data.data;
    }
  }
}
</script>



