<template>
	<section>
		<h1>Добавление персоны</h1>
		<form action="/api/person/add" method="POST">
			<div class=row>
				<div class="col-md-6">
					<div class="form-group">
						<label for="firstName">Имя</label>
						<input type="text" class="form-control" id="firstName" name="firstName" placeholder="логин" v-model="firstName">
					</div>
					<div class="form-group">
						<label for="lastName">Фамилия</label>
						<input type="text" class="form-control" id="lastName" name="lastName" placeholder="фамилия" v-model="lastName">
					</div>				
					<div class="form-group">
						<label for="middleName">Отчество</label>
						<input type="text" class="form-control" id="middleName" name="middleName" placeholder="отчество" v-model="middleName">
					</div>				
					<div class="form-group">
						<label for="passport">Удостовение личности</label>
						<input type="text" class="form-control" id="passport" name="passport" placeholder="серия и номер паспорта" v-model="passport">
					</div>				
					<div class="form-group">
						<label for="fin">FIN-код</label>
						<input type="text" class="form-control" id="fin" name="fin" placeholder="FIN-код" v-model="fin">
					</div>				
					<div class="form-group">
						<label for="address">Адрес</label>
						<input type="text" class="form-control" id="address" name="address" placeholder="ex. пр. Азербайджана, д. 30"  v-model="address">
					</div>
				</div>
				<div class="col-md-6">
					<div class="form-group">
						<label for="phone">Телефон</label>
						<input type="text" class="form-control" id="phone" name="phone" placeholder="ex. +994 12 123 45 67" v-model="phone">
					</div>				
					<div class="form-group">
						<label for="mobile">Моб. телефон</label>
						<input type="text" class="form-control" id="mobile" name="mobile" placeholder="ex. +994 50 765 43 21" v-model="mobile">
					</div>				
					<div class="form-group">
						<label for="description">Группа крови</label>
						<select class="form-control" id="bloodType" name="bloodType" v-model="bloodType">
							<option value="O(I) Rh−">O(I) Rh−</option>
							<option value="O(I) Rh+">O(I) Rh+</option>
							<option value="A(II) Rh−">A(II) Rh−</option>
							<option value="A(II) Rh+">A(II) Rh+</option>
							<option value="B(III) Rh−">B(III) Rh−</option>
							<option value="B(III) Rh+">B(III) Rh+</option>
							<option value="AB(IV) Rh−">AB(IV) Rh−</option>
							<option value="AB(IV) Rh+">AB(IV) Rh+</option>
						</select>
					</div>
					<div class="form-group">
						<label for="sex">Пол</label>
						<select class="form-control" id="sex" name="sex" v-model="sex">
							<option selected value="male">мужской</option>
							<option value="female">женский</option>
						</select>
					</div>				
					<div class="form-group">
						<label for="description">Доп. информация</label>
						<textarea class="form-control" id="description" name="description" rows="6" v-model="description"></textarea>
					</div>				
				</div>
			</div>
			<button type="button" @click="addPerson" class="btn btn-primary">Создать</button>
		</form>
	</section>
</template>

<script>
import PersonsService from '@/services/PersonsService'

export default {
  name: 'PersonAdd',
  data () {
    return {
    	firstName: '',
    	lastName: '',
    	middleName: '',
    	passport: '',
		fin: '',
		address: '',
		phone: '',
		mobile: '',
		sex: '',
		bloodType: '',
		description: ''
    }
  },
  methods: {
    async addPerson() {
      await PersonsService.addPerson({
    	firstName: this.firstName,
    	lastName: this.lastName,
    	middleName: this.middleName,
    	passport: this.passport,
		fin: this.fin,
		address: this.address,
		phone: this.phone,
		mobile: this.mobile,
		sex: this.sex,
		bloodType: this.bloodType,
		description: this.description
      });
      alert('Person added');
    }
  }
}
</script>



